.App-link {
  color: #61dafb;
}
main {
  width:100%;
  margin:0;
  padding:0;
}
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


@media only screen and (max-width:1150px) {
  html{
    font-size:12px;
  }
}
.header {
    min-height:100px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0px 100px;
    box-shadow: var(--shadow);
    align-items: center;
    background-color: var(--background-comp-color);
}

@media only screen and (max-width:1150px){
    .header {
        padding-left:20px;
    }

    h1 {
        font-size: 1.2rem;
    };
}
.country-card {
    font-weight: 300;
    background-color: var(--background-comp-color);
    box-shadow: var(--shadow);
    border-radius: 0.3rem;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    width:300px;
    margin:20px;
    text-align: left;
    
}

.country-details{
    padding:20px;
}
.flag{
    height:200px;
    width:100%;
    position: 50% 50%;
    background-size:contain;
    background-repeat: no-repeat;
    background-position: top;
}
.searchbar{
    margin-bottom:10px;
    max-width: 100vw;
    display:flex;
    flex-direction: row;
    align-items: center;
    background-color: var(--background-comp-color);
    padding:26px 20px;
    border-radius: 0.5em;
    box-shadow: var(--shadow);
    color: var(--text-color);

}
::-webkit-input-placeholder{
    color: var(--text-color);
}
:-ms-input-placeholder{
    color: var(--text-color);
}
::placeholder{
    color: var(--text-color);
}
.searchbar > input {
    max-width:100vw;
    color: var(--text-color);
    width: 300px;
    margin-left:20px;
    background-color: rgba(0,0,0,0);
    border:none;
    font-size:15px;

}
.searchbar > input:focus {
    outline: 1px solid white;
    outline-offset: 0.5em;
}

@media only screen and (max-width:1150px) {
    .searchbar {
        margin: 0px 0px;
        margin-bottom:30px;
        width: calc(100vw - 85px);
    }
}
.region-select {
    position: relative;
    margin-bottom:10px;
    width:240px;
    background-color: var(--background-comp-color);
    font-weight: 300;
    border-radius: 0.5em;
    padding:5px 0px;
    box-shadow: var(--shadow);
}
.cancel-button{
    margin-bottom:10px;
    margin-right:5px;
    background-color: var(--background-comp-color);
    width:65px;
    border: none;
    color: var(--text-color);
    border-radius:0.5em;
    font-size: 25px;
    box-shadow: var(--shadow);
}
.dropdown {
    position:absolute;
    height:200px;
    bottom:-205px;
    left:0;
    right:0;
    background-color: var(--background-comp-color);
    border-radius: 0.5em;
    display: flex;
    flex-direction: column;
    justify-content:flex-start;
    box-shadow: var(--shadow);
}

.dropdown div{
    padding:8px 30px;
    text-align: left;
    
}
.dropdown div:hover{
    background-color: aqua;
}
.region-outter{
    display:flex;
    flex-direction: row;
    border-radius:2em;
}

.region-select-top {
    background-color: var(--background-comp-color);
    border-radius:0.5em;
    display:flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center; 
    padding:20px 30px;
}

@media only screen and (max-width:1150px) {
    .region-outter{
        flex-direction: row-reverse;
    }

    .cancel-button{
        margin-left:5px; 
    }
}
.main {
    margin:auto;
    display:flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
}

.search-container{
    padding:50px 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
}

.main-container{
    padding:0px 40px;
}

@media only screen and (max-width:1150px) {
    .main-container{
        padding:0px 15px;
    }
    .search-container{
        padding:20px 0px;
        display: flex;
        align-items: flex-start;
        flex-direction: column;
        justify-content:center;
        flex-wrap: wrap;
    }

    .main {
        margin: 0px;
    }
}

.detailed{
    margin: 0px 50px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.detailed-body{
    display:flex;
    flex-direction: row;
    align-items: center;
    flex-basis: 0;
}

.detailed-body > div {
    flex-grow: 2;
    margin: 0px 50px;
    width:50%;
}



.detailed-flag{
    height:100%;
    max-height:600px;
    width:auto;
    min-width:100%;
    max-width: 100%;
    /* width:auto;
    height:auto;
    max-width:100%;
    max-height:100%; */
    box-shadow: var(--shadow);

}

.backbutton {
    margin:0px 45px;
    background-color: var(--background-comp-color);
    padding:12px 35px;
    width:130px;
    font-weight: 100;
    color: var(--text-color);
    border:none;
    box-shadow: var(--shadow);
}
.button-container{
    display:flex;
    padding:45px 0px;
}

.borderbutton {
    margin:5px 5px;
    background-color: var(--background-comp-color);
    padding:10px 10px;
    font-weight: 300;
    color: var(--text-color);
    border:none;
    box-shadow: var(--shadow);
}


.detailed-details{
    margin:50px 0px;
    font-weight: 300;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    flex-wrap: wrap;
    height:200px;
}
.detailed-details > div {
    display:block;
    height:30px;
    flex-basis: 20%;
}

.detailed-body {
    text-align: left;
}


@media only screen and (max-width:1150px) {

    .detailed{
        margin:0px 15px;
        width:calc(100vw - 30px);
    }
    .detailed-body{
        flex-direction: column;
        align-items: flex-start;
    }

    .detailed-body > div {
        flex-grow: 2;
        margin: 0px 0px;
        width:50%;
    }

    .detailed-flag{
        max-width: 90vw;
    }
    .detailed-body > div {
        width:100%;
    }
    .detailed-right{
        max-width:calc(100vw - 20px)
    }
    .backbutton{
        margin:0;
    }
}
